.container-github {
    justify-content: center;
    position: relative;
}

.container-github::after {
    content: 'On  the  Github';
    text-align: center;
    font-size: 72px;
    font-family: 'Bodoni Moda', serif;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 2.2rem;
    color: rgba(136, 165, 191, 0.48);
    position: absolute;
    top: 40%;
    opacity: .8;
}

@media screen and (max-width: 1096px) {
    .container-github::after {
        display: none;
    }
}