.frame {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--default-background-page)
}

.content {
    width: 80%;
    height: 80%;
    display: inherit;
    align-items: center;
    justify-content: center;
    background: var(--default-background-page);
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(255, 255, 255, 0.8) 0px 1px 1px 0px;
}