form input {
    border-radius: 0px;
    padding-left: 15px;
    font-size: 13px;
    font-weight: 300;
    color: #fff;
    background-color: rgba(250, 250, 250, 0.15);
    outline: none;
    border: none;
    box-shadow: none;
    line-height: 40px;
    height: 40px;
    width: 100%;
    margin-bottom: 25px;
}

form textarea {
    border-radius: 0px;
    padding-left: 15px;
    padding-top: 10px;
    font-size: 13px;
    font-weight: 300;
    color: #fff;
    background-color: rgba(250, 250, 250, 0.15);
    outline: none;
    border: none;
    box-shadow: none;
    height: 170px;
    max-height: 220px;
    width: 100%;
    max-width: 100%;
    margin-bottom: 25px;
    resize: none;
}

.icon {
    display: flex;
}

.icon ul {
    padding-left: 0;
}

.icon ul li {
    padding: 5px;
    display: flex;
    justify-content: flex-start;
}

.icon ul li i {
    font-size: 1.1rem;
    text-align: left;
    margin-top: 10px;
    width: 30px;
}

.icon ul li span {
    font-size: 1.3rem;
    width: 100%;
}