.activ-style {
    color: rgba(136, 165, 191, 0.48);
}

.activ-style::after {
    display: block;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 1.5px;
    width: 40%;
    background: rgba(136, 165, 191, 0.48);
    content: '';
}

.navigation-btn {
    line-height: 40px;
    font-size: 1.5rem;
    transition: 0.5s;
    font-family: 'Rajdhani', sans-serif;
}

.navigation-btn:hover {
    color: rgba(136, 165, 191, 0.48);
}

.navigation-btn:not(.activ-style):hover::after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    height: 1.5px;
    width: 40%;
    background: rgba(136, 165, 191, 0.48);
    transform: translate(-50%, -50%);
}

@media screen and (min-width: 1024px) {
    .activ-style::after {
        width: 60% !important;
    }

    .navigation-btn:not(.activ-style):hover::after {
        width: 60%;
    }
}