.container-card {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    margin: 20px;
    width: 230px;
    max-width: 250px;
    min-height: 300px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    z-index: 1;
}

.card-body {
    font-family: 'Bodoni Moda', serif;
    font-size: 4rem;
    font-weight: 900;
    text-align: center;
    margin: 10px;
}

.shadow {
    text-shadow: 2px -2px 8px rgba(136, 165, 191, 0.9);
}

.card-item {
    width: 100%;
    padding: 20px;
}

.card-content {
    font-size: 2rem;
    text-align: center;
    word-wrap: break-word;
}